 
.brand-title {
  font-family: 'Kaushan Script', cursive;
  font-size: large;
  font-weight: bolder; 
  }
   
  /* square buttons */
  .rec.rec-arrow {
    border-radius: 2px ;
    border: #eafaf4;
    background: #eafaf4;
}
/* round buttons on hover */
.rec.rec-arrow:hover {
    border-radius: 0;
    background: #eafaf4 !important;
}

.rec.rec-arrow:focus {
    background: #eafaf4 !important;
}
/* hide disabled buttons */
.rec.rec-arrow:disabled {
    visibility: hidden;
}
/* disable default outline on focused items */
/* add custom outline on focused items */
.rec-carousel-item:focus {
    outline: none;
    box-shadow: inset 0 0 1px 1px #eafaf4;
}

.rec.rec-dot {
    visibility: hidden;
    
  }

  .ReactIcon{
    font-size: 30px;
  }
  
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .ReactIcon {
      font-size: 15px;
    }
    .rec.rec-arrow {
        border-radius: 2px ;
        background: transparent;
        font-size: xx-small;
        padding: 5%;
        min-width: 25px;
        min-height: 25px;
        width: 25px;
        height: min-content;
        padding: 2px;
        
    }
  
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {.ReactIcon {
      font-size: 25px;
    }}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {.ReactIcon {
      font-size: 30px;
    }}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {.ReactIcon {
    font-size: 30px;
  }}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {.ReactIcon {
    font-size: 30px;
  }}
